<template>
  <div :class="['claseScope', state]"
    @click="change(info._id)">
    <header>
      <small>{{'Mdulo / Semana ' + index }}</small>
      <h3>{{ info.name }}</h3>
      <Button
        v-if="info.state"
        model="a2"
        size="ssl">
        <ChevronRight slot="default" />
      </Button>

      <Button
        v-if="!info.state"
        model="a2"
        size="ssl">
        <Lock slot="default" />
      </Button>

      <div class="time"
        v-if="info.time">
        <Countdown :time="info.time"
          @progress="reload">
          <template slot-scope="props">Disponible en: {{ props.days }} D, {{ props.hours }}:{{ props.minutes }}:{{ props.seconds }} </template>
        </Countdown>
      </div>
      <transition name="fade">
        <div class="message"
          v-show="message && !info.state">
          Tienes que esperar para ver este contenido.
        </div>
      </transition>
    </header>
  </div>
</template>

<script>
import Bus from '@/helpers/Bus'

import Button from '@/components/Button.vue'
import ChevronRight from 'vue-material-design-icons/ChevronRight.vue'
import Lock from 'vue-material-design-icons/Lock.vue'

import Countdown from '@chenfengyuan/vue-countdown';

export default {
  name: 'ClaseIndex',
  components:{
    Button,
    ChevronRight,
    Lock,
    Countdown
  },
  props:{
    info: Object,
    index: Number
  },
  computed:{
    state(){
      return this.info.state ? 'on' : 'off'
    }
  },
  data(){
    return{
      go: false,
      message: false
    }
  },
  methods:{
    reload (e) {
      if (e.seconds == 0) {
        setTimeout(() => {
          Bus.$emit('loadModules')
        }, 1000);
      }
    },
    change(module){
      if (module) this.goToModule(module)
      else this.alert()
    },
    goToModule(module) {
      // window.location.href = `/dash/${module}` 
      this.$router.push({ path: `/dash/${module}` })
      // window.location.reload()
    },
    alert() {
      this.message = true
      setTimeout(() => {
        this.message = false
      }, 3000);
    }
  }
}
</script>

