    }
    p{
      color: var(--color09);
      text-align: left;
    }
  }

  .on{
    color: var(--color01);
  }

  .off{
    color: var(--color09);
    .btn{
      color: var(--color09);
    }
  }

  .time{
    position: absolute;
    right: 4em;
    @include mainEnd;
    @include fromTo(s,l){
      left: 0;
      top: 2.9em;
      font-size: 0.9em;
      display: table;
    }
  }

  .fade-enter-active, .fade-leave-active {
    transition: all .5s;
    opacity: 1;
    transform: translate(0, 0)
  }
  .fade-enter, .fade-leave-to{
    transition: all .5s;
    opacity: 0;
    transform: translate(0, 3em)
  }

  .message{
    position: absolute;
    right: 4em;
    background-color: var(--color00);
    padding: 0.5em;
    @include softShadow;
    border-radius: 0.5em;
  }


</style>
