<template>
  <div class="recoverArea">
      <form action="preventDefault">
        <h3 >Ingrese su nueva Contrasea</h3>
        <input type="password" placeholder="Contrasea" name="password" required v-model="user.password">
        <input type="text" placeholder="Confirmar Contrasea" name="password" required v-model="user.confirmPassword">
        <div class="messageArea">
          <p v-if="message">{{ message }}</p>
        </div>
        <Button
          value="Enviar"
          model="a0"
          size="l"
          @click.native="resetPassword"
        />
      </form>
    </div>
</template>

<script>
import Button from '@/components/Button.vue'
export default {
  name: 'Recover',
  components: {
    Button
  },
  data () {
    return {
      message: '',
      user: {
        password: '',
        confirmPassword: '',
        token: ''
      }
    }
  },
  methods: {
    validateEmail () {
      if (this.password !== this.confirmPassword) {
        return false
      }
      return true
    },
    resetPassword () {
      if (this.validateEmail()) {
        this.user.token =  this.$route.params.token
         this.$store.dispatch('reset_password', this.user)
         .then((res)=> {
           this.message = res
            setTimeout(() => {
                this.$router.push('/')
            }, 2000);
         })
         .catch((err)=> {
           this.message = err
            setTimeout(() => {
              this.$router.push('/')
            }, 4000);
         })
      }
    }
  }
}
</script>

