<template>
  <transition-group  :class="['logCard', slim]" 
      name="fade"
      mode="out-in"
      tag="div">
      <div key="1"
        class="data"  
        v-if="edit">
        <h3>Tu Perfil</h3>
        <h5> {{ user.userName }}, {{ user.userLastName }}</h5>
      </div>
      <div class="messageArea" key="4">
          <p class="guest" v-if="$store.getters.user.type == 'guest'">
            Tienes que activar tu cuenta. Revisa tu email y Hace cliec en el enlace!
          </p>
          <p class="confirmedPlus" v-if="$store.getters.user.type == 'confirmedPlus'">
            Tu pago esta pendiente de revision, apenas sea confirmado podras acceder al curso completo!
          </p>
      </div>
      <div 
        key="2"
        class="actions">
        <Button
          v-if="$store.getters.user.type == 'confirmed'"
          class="conf"
          value="Confirmar Pago"
          model="a3"
          size="l"
          @click.native="pay"
        />
        <div>
        <Button
          class="edit"
          value="Editar"
          model="a1"
          size="l"
          @click.native="swich"
        />
        <Button
          class="exit"
          value="Salir"
          model="a4"
          size="l"
          @click.native="exit"
        />
        </div>
      </div>
      <form
        key="3"
        action="preventDefault"
        v-if="edit">
        <input type="text" placeholder="Nombre" v-model="user.userName" name="name">
        <input type="text" placeholder="Apeliido" v-model="user.userLastName" name="lastname" >
        <input type="email" placeholder="Email" v-model="user.email" name="email">
      <div class="space" />
        <Button
          value="descartar"
          model="a1"
          size="l"
          class="discart"
          @click.native="reset"
        />
        <Button
          value="Guardar"
          model="a0"
          size="l"
          class="save"
          @click.native="save"
        />
      </form>
      <!-- <code key="9">{{ $data}}</code> -->
    </transition-group>
</template>

<script>

import Bus from '@/helpers/Bus'
import Button from '@/components/Button.vue'

export default {
  name: 'UserWin',
  components: {
    Button
  },
  data () {
    return {
      edit: false,
      post:false,
      user: {
        _id: this.$store.getters.user._id,
        userName: this.$store.getters.user.userName,
        userLastName: this.$store.getters.user.userLastName,
        email: this.$store.getters.user.email
      },
      emailCopy:  this.$store.getters.user.email
    }
  },
  computed: {
    slim () {
      return this.edit ? 'slim' : ''
    }
  },
  methods: {
    swich () {   
      this.edit = !this.edit
    },
    exit () {   
      this.$store.dispatch('sign_out')
      .then(()=>{ 
        Bus.$emit('logOut');
        Bus.$emit('userLogOut');
        Bus.$emit('reset');
        this.$router.push({ path: `/` })
      })
      .catch((err)=>{ console.log('error al salir' + err )})
    },
    reset () {
     this.user = {
        _id: this.$store.getters.user._id,
        userName: this.$store.getters.user.userName,
        userLastName: this.$store.getters.user.userLastName,
        email: this.$store.getters.user.email
      }
    },
    save () {
      this.$store.dispatch('edit_user', this.user)
      .then(() => {
        if (this.user.email == this.emailCopy)  Bus.$emit('userLogIn')
        else this.exit()
      })
    },
    pay () {
      let id = this.$store.getters.user._id
       this.$http.post('user/payMessage/'+id,
        { headers: {'Authorization': `Bearer ${this.$store.state.session.token}`} })
        .then(res => {
          this.$store.dispatch('fetch_user', {token: this.$store.state.session.token})
        })
        .catch(err => {
          console.log(err);
        })
    }
  },
  mounted () {
  }
}
</script>

