<template>
  <div class="logCard">
      <form action="preventDefault">
        <h3 >Inicio de sesin</h3>
        <input
          type="mail" 
          v-model="user.email"
          placeholder="Email"
          name="mail"
          required>
        <input type="pasword" v-model="user.password" placeholder="Contrasea" name="password" required>
        <div class="messageArea">
          <p class="ok" v-if="inn">{{ inn }}</p>
          <p class="err" v-if="err">{{ err }}</p>
        </div>
        <Button
          value="Olvidaste tu contrasea?"
          model="a2"
          size="m"
          emmit="recoPass"
        />
        <Button
          value="Ingresar"
          model="a0"
          size="l"
          @click.native="signIn"
        />
      </form>
      <div class="createArea">
        <p>An no tienes Usuario?</p>
        <Button
          value="Hac clic aqu."
          model="a2"
          size="m"
          emmit="signUp"
        />
      </div>
      <!-- <code>{{$data}}</code> -->
    </div>
</template>

<script>

import Bus from '@/helpers/Bus'
import Button from '@/components/Button.vue'

export default {
  name: 'LogInWin',
  components: {
    Button
  },
  data () {
    return {
      inn: '',
      err: '',
      user: {
        email: 'mauro.taliente3@gmail.com',
        password: 'hola'
      },
      loading: false
    }
  },
  methods: {
    preventDefault (e) {
      return e.preventDefault()
    },
    signIn (e) {
      e.preventDefault()
      this.loading = true
      this.$store.dispatch('sign_in', this.user)
      .then(() => {
        this.loading = false
        this.inn = `Bienvenido/a nuevamente, ${this.$store.getters.user.username}!`
        this.inn = '' 
        Bus.$emit('userLogIn')
        Bus.$emit('logIn')
        Bus.$emit('userWin')
        Bus.$emit('reset')
        if (this.$store.getters.user.type !== 'admin') this.$router.push({ path: `/dash` })
        else this.$router.push({ path: `/admin` })
      }).catch((err) => {
        this.loading = false
        console.log(err);
        this.err = `Error en tu contrasea o mail`
        setTimeout(() => {  this.err = '' }, 3000);
      })
    }
  }
}
</script>

