<script>

import BienvenidaCurso from '@/components/BienvenidaCurso.vue'

export default {
  name: 'Dash',
  components: {
    BienvenidaCurso
  },
  methods: {
    auth() {
      setTimeout(() => {
        if (!this.$store.getters.is_authenticated) this.$router.push({ path: `/` })
      }, 1500);
    }
  },
  mounted() {
    this.auth()
  },
}
</script>

