<script>
import Bus from '@/helpers/Bus'

import Button from '@/components/Button.vue'
import BarraProgreso from '@/components/BarraProgreso.vue'
import MessageIcon from 'vue-material-design-icons/MessageText.vue'
import Next from  '@/components/Next.vue'

export default {
  name: 'BienvenidaCurso',
  components:{
    Button,
    BarraProgreso,
    MessageIcon,
    Next
  },
  data() {
    return {
      p: true,
      s: {}
    }
  },
  methods:{
    back() {
     this.$router.push({ path: `/dash` })
    },
    checkP () {
      if  (this.$route.fullPath !== '/dash/' &&
           this.$route.fullPath !== '/dash')  {this.p = false}
      else {this.p = true}
    },
    next (s) {
      if (s) this.s = s;
      this.checkP()
    }
  },
  mounted() {
    Bus.$on('nextData', this.next);
    this.checkP()
  }
}
</script>

