<template>
  <div class="recoverArea">
    <h3 v-if="message">{{ message }}</h3>
  </div>
</template>

<script>
export default {
  name: 'Confirmed',
  components: {
  },
  data () {
    return {
      message: '',
      user: {}
    }
  },
  methods: {
    confirmed () {
      let  _this = this
      const token =  this.$route.params.token
      _this.$http.get(`/confirmedpassword/${token}`)
      .then((res)=> {
        this.message = 'Usuario confirmado con exito'
        setTimeout(() => {
            this.$router.push('/')
        }, 2000);
      })
      .catch((err)=> {
        this.message = 'No se pudo confirmar al Usuario'
        setTimeout(() => {
          this.$router.push('/')
        }, 4000);
      })
    }
  },
  mounted () {
    this.confirmed()
  },
}
</script>

