<template>
  <div class="redesScope">
    <a :class="btnStyle" href=""><Facebook /></a>
    <a :class="btnStyle" href=""><Youtube /></a>
    <a :class="btnStyle" href=""><Twitter /></a>
  </div>
</template>

<script>

import Facebook from 'vue-material-design-icons/Facebook'
import Youtube from 'vue-material-design-icons/Youtube'
import Twitter from 'vue-material-design-icons/Twitter'

export default {
  name: 'Redes',
  components: {
    Facebook,
    Youtube,
    Twitter
  },
  props: {
    type: String
  },
  computed: {
    btnStyle () {
      if (this.type == 'extra') return 'btnBlue'
      if (this.type == 'footer') return 'btnGreen'
      return 'btnDefault'
    }
  }
}
</script>

