<template>
  <span 
    :class="['btn', modelClass, size, group, modelClass+stateClass]" 
    :style="{margin: margin}"
    @click="toggle">
    <slot name="defaultPre" v-if="!dualActive"/>
    <slot name="activePre" v-if="dualActive"/>
    {{ value }}
    <slot name="default" v-if="!dualActive"/>
    <slot name="active" v-if="dualActive"/>
  </span>
</template>

<script>
import Bus from '@/helpers/Bus'
export default {
  name: 'Button',
  props: {
    value: String,
    size: String,
    group: String,
    swich: Boolean,
    dual: Boolean,
    model: String,
    margin: String,
    emmit: String,
    on: Boolean,
  },
  data () {
    return {
      active: false,
      dualActive: false
    }
  },
  computed: {
    stateClass () {
      return (this.active) ? 'active' : ''
    },
    modelClass () {
      if (this.$props.model == 'a0') return 'alternative00'
      if (this.$props.model == 'a1') return 'alternative01'
      if (this.$props.model == 'a2') return 'alternative02'
      if (this.$props.model == 'a3') return 'alternative03'
      if (this.$props.model == 'a4') return 'alternative04'
      if (this.$props.model == 'a5') return 'alternative05'
      if (this.$props.model == 'a6') return 'alternative06'
      return ''
    }
  },
  methods: {
    toggle () {
      if (this.$props.emmit) {
        Bus.$emit(this.$props.emmit)
      }
      if (this.$props.group) {
        Bus.$emit(this.$props.group)
        this.active = true
      }
      if (this.$props.swich && !this.$props.group) {
        this.active = !this.active
      }
      if (this.$props.dual) {
        this.dualActive = !this.dualActive
      }
    },
    reset () {
      this.active = false
      this.dualActive = false
    }
  },
  created () {
    Bus.$on(this.$props.group, this.reset)
  },
  mounted () {
    Bus.$on('reset', this.reset)
    if (this.$props.on) this.active = true
  }
}
</script>

