<template>
  <div class="logCard">
      <form action="preventDefault">
        <h3 >Recuperar Contrasea</h3>
        <h5>Estas por recuperar la contrasea de tu cuenta.</h5>
        <input type="mail" placeholder="Email" name="mail" required v-model="user.email">
        <div class="messageArea">
          <p v-if="error">{{ error }}</p>
        </div>
        <Button
          value="Enviar"
          model="a0"
          size="l"
          @click.native="resetPassword"
        />
      </form>
      <div class="createArea">
        <p>An no tienes Usuario?</p>
        <Button
          class="reg"
          value="Hac clic aqu."
          model="a2"
          size="m"
          emmit="signUp"
        />
      </div>
    </div>
</template>

<script>
import Button from '@/components/Button.vue'
export default {
  name: 'RecoverPass',
  components: {
    Button
  },
  data () {
    return {
      error: '',
      user: {
        email: ''
      }
    }
  },
  methods: {
    validateEmail () {
      // eslint-disable-next-line
      let regex = new RegExp('^[a-z0-9]+(\.[_a-z0-9]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,15})$')
      if (!regex.exec(this.user.email)) {
        return false
      }
      return true
    },
    resetPassword () {
      if (this.validateEmail()) {
         this.$store.dispatch('reset_password_req', this.user)
         .then(() => this.error = 'Se envio un email con un link para recuperar la contrasea')
         .catch(() => this.error = 'El Email enviado no corresponde a un Usuario Registrado')   
      }
    }
  }
}
</script>

