<template>
  <div class="next" v-if="live">
    <div :class="['content', state, size ]"
      @click="goToNext(next._id)">
      <small> Mdulo / {{ next.title }}</small>
      <h4 > {{ next.name }}</h4>
      <Arrow class="fl" />
    </div>
        <!-- <code><pre>{{$data.permision}}</pre></code> -->
  </div>
</template>

<script>

import Arrow from 'vue-material-design-icons/ArrowRight.vue'
import Bus from '@/helpers/Bus'

export default {
 name: 'Next',
 components:{
   Arrow
},
 props:{
   info: Object,
   small: Boolean
 },
 computed: {
   state () {
    return this.permision ? 'on' : 'off'
   },
   size () {
     return this.small ? 's' : ''
   }
 },
 data () {
   return {
     next: {
       _id: '',
       name: '',
       info: '',
       title: '',
       type: ''
     },
     permision: false,
     live: true
   }
 },
 methods: {
   setNext(e) {
     
     if ( e && (this.$store.getters.user_type == 'pro' || e.type == 'confirmed' || this.$store.getters.user_type == 'admin') ) {
      this.next = e
      this.$http.get('modules/'+ this.$store.getters.user._id,
      { headers: {'Authorization': `Bearer ${this.$store.state.session.token}`} })
      .then(res => {
        let available = res.data.modules.filter(x =>  x.state == true)
        let ok = available.findIndex(x =>  x._id.toString() == e._id.toString())
        if (ok !== -1) this.permision = true
      })
     } else {
       this.live = false
     }
   },
   goToNext (module) {
      let acepted = this.permision
      if (acepted) {
        //  window.location = `/dash/${module}` 
        this.$router.push({ path: `/dash/${module}` })
        //  window.location.reload()
      }
   }
 },
 mounted() {
   Bus.$on('next', this.setNext)
 },
}
</script>

