<template>
  <div class="adminArea">
    <div class="adminContent">
      <header>
        <h2>Lista de usuarios</h2>
        <input v-model="search" type="text" placeholder="Filtrar por email">
        <Button class="marg" size="s" model="a0" value="recargar"
                @click.native="getUsers()" />
      </header>
      <body>
        <li
          v-for="(user, index) in filterUsers" :key="index"
          class="cardArea">
          <div class="card">
            <h4>{{user.userName}} | {{user.email}} | <small>{{user.signUpDate}}</small></h4>
            <div class="action">
              <Button class="marg" size="s" model="a3" value="activar"
                v-if="user.type == 'confirmed'"
                @click.native="swhichtUserType(user)"
              />
              <Button class="marg des" size="s" model="a2" value="desactivar"
                v-if="user.type == 'pro'"
                @click.native="swhichtUserType(user)"
              />
              <Button class="marg" size="s" model="a2" value="eliminar"
                @click.native="confirmDel(user._id)"
              />
              
              <div class="confirm"
                v-if="user.delete">
                <Button class="marg" size="s" model="a2" value="descartar"
                  @click.native="confirmDel(user._id)"
                />
                <Button class="marg" size="s" model="a4" value="confirmar"
                  @click.native="deletetUser(user)"
                />
              </div>
            </div>
          </div>
        </li>
      </body>
    </div>
    <!-- <code><pre>{{$data}}</pre></code> -->
  </div>
</template>

<script>
import Bus from '@/helpers/Bus'
import Button from '@/components/Button.vue'

export default {
  name: 'Admin',
  components: {
    Button
  },
  data() {
    return {
      search: '',
      users: []
    }
  },
  computed: {
    filterUsers (){
      return this.users.filter((user) => {
        return user.email.match(this.search);
      })
    }
  },
  methods: {
    getUsers () {
      this.$http.get('users',
      { headers: {'Authorization': `Bearer ${this.$store.state.session.token}`} })
      .then(res => {
        this.users = res.data.users
        // console.log(res.data.users);
      }).catch(err => {
        console.log(err);
      })
    },
    swhichtUserType (user) {
      let type;
      if (user.type == 'pro') type = 'confirmed';
      if (user.type == 'confirmed') type = 'pro';

      this.$http.patch(`user/edit/${user._id}`, {type: type},
      { headers: {'Authorization': `Bearer ${this.$store.state.session.token}`} })
      .then(res => {
        this.getUsers()
      }).catch(err => {
        console.log(err);
      })
    },
    deletetUser (user) {
      this.$http.delete(`user/delete/${user._id}`,
      { headers: {'Authorization': `Bearer ${this.$store.state.session.token}`} })
      .then(res => {
        this.getUsers()
      }).catch(err => {
        console.log(err);
      })
    },
    confirmDel (key) {
      let usersTemp = [... this.users]
      let ele = usersTemp.findIndex(x => x._id == key);
      let selected = usersTemp[ele]
      selected.delete = !selected.delete
      usersTemp[ele] = selected;

      this.users =  usersTemp
    }
  },
  mounted() {
    Bus.$on('ready', this.getUsers)
  },
}
</script>

