import Vue from 'vue'
import Vuex from 'vuex'
import Bus from '@/helpers/Bus'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    session: {
      token: null,
      user: new Object(),
      available: 0
    }
  },
  getters: {
    is_authenticated: state => {
      return state.session.token ? true : false
    },
    user_type: state => {
      return state.session.user.type //completar
    },
    user: state => {
      return state.session.user
    },
    user_id: state => {
      return state.session.user._id
    },
    available: state => {
      return state.session.available
    }
  },
  mutations: {
    set_session_token (state, token) {
      state.session.token = token
    },
    set_session_user (state, user) {
      state.session.user = user
    },
    set_available (state, available) {
      state.session.available = available
    }
  },
  actions: {
    sign_in (context, payload) {
      let _this = this
      return new Promise((resolve, reject) => {
        _this._vm.$http.post('/signin', payload)
        .then((res) => {
          context.dispatch('fetch_user', res.data)
          .then(() => {
            resolve()
          })
        }).catch((err) => {
          if (err.response && err.response.status === 401) {
            reject({type: 'warning', message: 'El usuario y/o la contrasea son incorrectos, revise sus datos de acceso y vuelva a intentarlo.'})
          } else {
            console.error('Connection error!', err)
            reject({type: 'danger', message: 'Ha ocurrido un error al iniciar sesin. Vuelva a intentarlo en unos minutos.'})
          }
        })
      })
    },
    edit_user (context, payload) {
      let _this = this
      return new Promise((resolve, reject) => {
        console.log(payload);
        
        _this._vm.$http.post('/user/edit/'+payload._id, payload)
        .then(() => {
          context.dispatch('check_session')
          .then(() => {
            resolve()
          })
        }).catch((err) => {
          if (err.response && err.response.status === 401) {
            reject({type: 'warning', message: 'El usuario y/o la contrasea son incorrectos, revise sus datos de acceso y vuelva a intentarlo.'})
          } else {
            console.error('Connection error!', err)
            reject({type: 'danger', message: 'Ha ocurrido un error al iniciar sesin. Vuelva a intentarlo en unos minutos.'})
          }
        })
      })
    },
    sign_out (context) {
      context.commit('set_session_token', null)
      context.commit('set_session_user', new Object())
      localStorage.comer_despierto_token =  null
    },
    fetch_user (context, payload) {
      let _this = this
      return new Promise((resolve, reject) => {
        _this._vm.$http.get(`/token/${payload.token}`, { headers: {'Authorization': `Bearer ${payload.token}`} })
        .then((res) => {
          context.commit('set_session_token', payload.token)
          context.commit('set_session_user', res.data)
          resolve()
        })
        .catch((err) => {
          Bus.$emit('logOut');
          Bus.$emit('userLogOut');
          Bus.$emit('reset');
          console.error('Connection error!', err)
          reject(err)
        })
      })
    },
    check_session (context) {
      let _token =  localStorage.comer_despierto_token
      return new Promise((resolve, reject) => {
        if (localStorage.comer_despierto_token) {
          context.dispatch('fetch_user', {token: _token})
          .then(() => {
            console.log('Authenticated from cookie!')
            resolve()
          })
          .catch(() => {
            localStorage.removeItem('comer_despierto_token');
            console.log('Cookie expired!')
            reject()
          })
        }
      })
    },
    reset_password_req (context, payload) {
      let _this = this 
      return new Promise((resolve, reject) => {
        _this._vm.$http.post(`/reqrecover/`, payload)
          .then(() => {
            console.log('Send Email!')
            resolve()
          })
          .catch(() => {
            console.log('Error al enviar el Email!')
            reject()
          })
      })
    },
    reset_password (context, payload) {
      let _this = this
      console.log(payload);
      return new Promise((resolve, reject) => {
        _this._vm.$http.post(`/recover`, payload)
          .then((res) => {
            console.log('Su contrasea fue cambiada con exito: ')
            resolve(res.data.msg)
          })
          .catch((err) => {
            console.log('Error cambiar la contrasea')
            reject(err.response.data.msg)
          })
      })
    }
  }
})

store.watch((state) => state.session.token, () => {
  Bus.$emit('ready')
  Bus.$emit('loadModule');
  Bus.$emit('loadModules');
  store._vm.$http.defaults.headers.common['Authorization'] =  `Bearer ${store.state.session.token}`
  if (store.state.session.token) {
    localStorage.comer_despierto_token =  store.state.session.token
  }
})

export default store