<template>
    <div class="tablaScope">
      <h3 class="title">Tabla de contenidos</h3>
      <ul>
        <li v-for="(video, index) in videos" :key="index"
          @click="select(video.url, index+1)">
          <Button
           :on="first(index+1)"
           model="a5"
           size="sl"
           group="videos">
          {{ video.title }}
          </Button>
        </li>
      </ul>
    </div>
</template>

<script>

import Bus from '@/helpers/Bus'
import Button from '@/components/Button.vue'

export default {
  name: 'TablaContenidos',
  components:{
    Button
  },
  props: {
    videos: Array,
    module: String
  },
  methods: {
    select(url, pos) {
      Bus.$emit('goVide', url);
      if (pos == this.videos.length) this.finishedModule()
    },
    first (id) {
      if (id == 1) { return true }
      else { return false }
    },
    finishedModule () {
      let userId = this.$store.getters.user._id
      let idModule = this.module
      this.$http.post('user/finished/'+userId, {module: idModule},
      { headers: {'Authorization': `Bearer ${this.$store.state.session.token}`} })
      .then(e => {
        console.log(e.data.message);
      })
    }
  },
  mounted() {
    Bus.$on('finished', this.finishedModule)
  },
}
</script>

