import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import axios from 'axios'

Vue.config.productionTip = false //andubo en true

// import "vue-material-design-icons/styles.css"

Vue.prototype.$http = axios
Vue.prototype.$http.defaults.baseURL = process.env.VUE_APP_API || 'http://localhost:3000/api'

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
