<template>
  <transition name="fadeExtra" mode="out-in" tag="div">
    <div
     key="0"
      class="mainScope"
      v-if="display">
      <div class="close" key="6">
        <CloseIcon />
      </div>
      <div
        class="logScope">
        <transition-group name="fadeExtra" mode="out-in" tag="div">
          <LogInWin
            key="1"
            v-if="logIn"
          />
          <UserWin
            key="2" 
            v-if="userWin"
          />
          <SignUpWin
            key="3" 
            v-if="signUp"
          />
          <RecoverPass
            key="4"
            v-if="recoPass"
          />
          <Quiero
            key="5"
            v-if="quiero"
          />
        </transition-group>
      </div>
      <div class="backExit"
        @click="resett"
      />
    </div>
  </transition>
</template>

<script>

import Bus from '@/helpers/Bus'
import LogInWin from '@/components/LogInWin.vue'
import SignUpWin from '@/components/SignUpWin.vue'
import UserWin from '@/components/UserWin.vue'
import RecoverPass from '@/components/RecoverPass.vue'
import Quiero from '@/components/Quiero.vue'
import CloseIcon from 'vue-material-design-icons/Close.vue'

export default {
  name: 'Window',
  components: {
    LogInWin,
    RecoverPass,
    SignUpWin,
    UserWin,
    Quiero,
    CloseIcon
  },
  data () {
    return {
      display: false,
      logIn: true,
      signUp: false,
      userWin: false,
      recoPass: false,
      quiero: false,
    }
  },
  methods: {
    resett () {
      this.display = !this.display
    },
    swich (state) {
      this.display = true
      this.signUp  = false
      this.userWin = false
      this.recoPass= false
      this.logIn   = false
      this.quiero  = false
      this[state]  = true
    }
  },
  mounted () {
    Bus.$on('logIn', () => this.swich('logIn'))
    Bus.$on('signUp', () => this.swich('signUp'))
    Bus.$on('userWin', () => this.swich('userWin'))
    Bus.$on('recoPass', () => this.swich('recoPass'))
    Bus.$on('profile', () => this.swich('profile'))
    Bus.$on('quiero', () => this.swich('quiero')) //
    Bus.$on('reset', ()=> this.resett())
  }
}
</script>

