import Vue from 'vue'
import Router from 'vue-router'
import Home from './views/Home.vue'
import Dash from './views/Dash.vue'
import Recover from './views/Recover.vue'
import Confirmed from './views/Confirmed.vue'
import Admin from './views/Admin.vue'

import CursoDash from './components/CursoDash.vue'
import Modulo from './components/Modulo.vue'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/admin',
      name: 'admin',
      component: Admin
    },
    {
      path: '/recover/:token',
      name: 'recover',
      component: Recover
    },
    {
      path: '/confirmed/:token',
      name: 'confirmed',
      component: Confirmed
    },
    {
      path: '/dash',
      name: 'dash',
      component: Dash,
      children: [
        {
          path: '/dash/',
          name: 'cursoDash',
          component: CursoDash
        },
        {
          path: '/dash/:id',
          name: 'modulo',
          component: Modulo
        }
      ]
    }
  ]
})
