<template>
   <body>
    <IntroModulo
      :info="module"
    />
     <section class="videoArea">
       <Loader v-if="loading" />
       <div class="videoScope">
          <div class="video">
            <iframe v-if="selectUrl" :src="selectUrl"
              style="position:absolute;top:0;left:0;width:100%;height:100%;" frameborder="0"
              allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen
              @click.native="finished">
            </iframe>
        </div>
       </div>
       <div class="clasesScope">
        <TablaContenidos 
          :module="module._id"
          :videos="module.videos" />
       </div>
     </section>
     <section class="tools">
       <ExtraModulo :info="module.materials"/>
       <div class="side">
         <Redes type="extra"/>
        <BookHome />
       </div>
     </section>
     <!-- <code><pre>{{$data.next}}</pre></code> -->
   </body>
</template>

<script>

import IntroModulo from '@/components/IntroModulo.vue'
import TablaContenidos from '@/components/TablaContenidos.vue'
import ExtraModulo from '@/components/ExtraModulo.vue'
import BookHome from '@/components/BookHome'
import Redes from '@/components/Redes'
import Loader from '@/components/Loader'

import Bus from '@/helpers/Bus'
 
export default {
  name: 'Modulo',
  components:{
    IntroModulo,
    TablaContenidos,
    ExtraModulo,
    BookHome,
    Redes,
    Loader
  },
  computed: {
  },
  data () {
    return {
      module: {},
      loading: false,
      selectUrl: '' // welcome
    }
  },
  methods:{
    getModule() {
      this.liading = true
      let id = this.$router.history.current.params.id
      if (id) {
        this.$http.get('module/'+id,
        { headers: {'Authorization': `Bearer ${this.$store.state.session.token}`} })
        .then(res => {
          this.module = res.data.module
          this.changeVideo(res.data.module.videos[0].url) 
          Bus.$emit('next',res.data.next)
          Bus.$emit('nextData', res.data.next);
          this.liading = false
        })
        .catch(err => {
          console.log(err);
        })
        }
    },
    changeVideo(url) {
      this.selectUrl = url
    },
    finished(){
      Bus.$emit('finished')
    },
  },
  mounted() {
    Bus.$on('goVide', this.changeVideo)
    Bus.$on('loadModule', this.getModule)
  }
}
</script>

