<template>
  <section class="promo">
    <div class="promoScope">
      <div class="videoCard">
        <div id="vimeo" class="video" style="padding:56.25% 0 0 0;position:relative;">
          <iframe src="https://player.vimeo.com/video/94964013?title=0&byline=0&portrait=0"
            style="position:absolute;top:0;left:0;width:100%;height:100%;" frameborder="0"
            webkitallowfullscreen mozallowfullscreen allowfullscreen>
          </iframe>
        </div>
      </div>
      <div class="dataCard">
        <div class="data">
          <h2>
            Curso Comer Despierto. <br> (8 Semanas)
          </h2>
          <div class="value">
            <p>Precio especial de:</p>
            <h2>AR$ {{ price }}</h2>
          </div>
          <div class="social">
            <Button
              class="first"
              size="i"
              model="a1">
              <MessageIcon />
            </Button>
            <Button
              size="i"
              model="a1">
              <Share />
            </Button>
          </div>
          <Button
            class="quiero"
            size="xl"
            value="Quiero tomar el curso"
            model="a4"
            @click.native="goTo"
          />
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Bus from '@/helpers/Bus'
import Button from '@/components/Button.vue'
import MessageIcon from 'vue-material-design-icons/MessageText.vue'
import Share from 'vue-material-design-icons/Share.vue'

export default {
  name: 'Promo',
  components:{
    Button,
    MessageIcon,
    Share
  },
  data () {
    return {
      price: '9.000',
      playerReady: true
    }
  },
  methods: {
    goTo() {
      if (this.$store.getters.is_authenticated) this.$router.push({ path: `/dash` })
      else Bus.$emit('quiero')
    }
  },
  mounted() {
      if (this.playerReady) return; // was already loaded
      let scriptTag = document.createElement("script");
      scriptTag.src = "https://player.vimeo.com/api/player.js";
      scriptTag.id =  "vimeo";
      document.getElementsByTagName('head')[0].appendChild(scriptTag);

    this.$nextTick(() =>{
      window.addEventListener('resize', this.getWindowWidth);
      this.getWindowWidth();
    })
  }
}
</script>

