<template>
  <transition name="fade">
    <main class="user">
      <Button
        v-show="out"
        class="ing"
        value="Ingresar"
        emmit="logIn"
        model="a1"
        size="l" />
      <Button
        v-show="inn"
        class="ing"
        emmit="userWin"
        model="a2"
        size="xl"
      >
      {{ user.username }}
      <Check
        slot="default"
        v-if="user.state && inn"
        class="indi" />
      <NoConfirmed
        slot="default"
        v-if="!user.state && inn"
        class="indi" />
      </Button>

        <!-- <code> {{ $data }}</code> -->
    </main>
  </transition>
</template>

<script>

import Bus from '@/helpers/Bus'
import Button from '@/components/Button.vue'
import Check from 'vue-material-design-icons/Check.vue'
import NoConfirmed from 'vue-material-design-icons/CircleMedium.vue'
export default {
  name: 'User',
  components: {
    Button,
    Check,
    NoConfirmed
  },
  data() {
    return {
      out: true,
      inn: false,
      user: {
       username: '',
      }
    }
  },
  methods:{
    logIn () {
      this.out = false
      this.inn = true
      this.user.username = this.$store.getters.user.userName
    },
    logOut () {
      this.out = true
      this.inn = false
      this.user.username = ''
    }
  },
  mounted() {
    this.$store.dispatch('check_session')
    .then(() =>{
      this.logIn()
    })
    Bus.$on('userLogOut', this.logOut)
    Bus.$on('userLogIn', this.logIn)
  }
}
</script>

