<template>
  <div class="logCard">
      <form action="preventDefault">
        <h3 >Registrate</h3>
        <h5>Necesitamos que nos des algunos datos personales.</h5>
        <input
          type="text"
          placeholder="Nombre"
          name="userName"
          v-model="user.userName"
          required
          @keyup="err = null"
        >
        <input
          type="text"
          placeholder="Apellido"
          name="userLastName"
          v-model="user.userLastName"
          required
          @keyup="err = null"
          >
        <input
          type="text"
          placeholder="Email"
          name="email"
          v-model="user.email"
          required
          @keyup="err = null"
        >
        <input
          type="password"
          placeholder="Contrasea"
          name="password"
          v-model="user.password"
          required
          @keyup="err = null"
        >
        <input
          type="password"
          placeholder="Confirmar Contrasea"
          name="confirmPasword"
          v-model="user.confirmPasword"
          required
          @keyup="err = null"
        >
        <div class="messageArea">
          <p class="ok" v-if="inn">{{ inn }}</p>
          <p class="err" v-if="err">{{ err }}</p>
        </div>
        <Button
          class="reg"
          value="Registrarme"
          model="a0"
          size="l"
          @click.native="signUp"
        />
      </form>
      <div class="createArea">
        <p>Ya tienes una cuenta?</p>
        <Button
          value="Iniciar sesion."
          model="a2"
          size="m"
          emmit="logIn"
        />
        </div>
      <!-- <code>{{$data.user}}</code> -->
    </div>
</template>

<script>

import Bus from '@/helpers/Bus'
import Button from '@/components/Button.vue'

export default {
  name: 'SingUpWin',
  components: {
    Button
  },
  data () {
    return {
      err: null,
      loading: false,
      inn: '',
      passwordVerified: false,
      user: {
        userName: 'Mauro',
        userLastName: 'Tali',
        email: 'mauro@kodev.co',
        password: 'hola',
        confirmPasword: 'hola',
        type: 'guest'
      },
      meta: {
        // eslint-disable-next-line
        userName: { pattern: '^[A-Za-z\s]+$', alias: 'Nombre' },
        // eslint-disable-next-line
        userLastName: { pattern: '^[A-Za-z\s]+$', alias: 'Apellido' },
        // eslint-disable-next-line
        email: { pattern: '^[a-z0-9]+(\.[_a-z0-9]+)*@[a-z0-9-]+(\.[a-z0-9-]+)*(\.[a-z]{2,15})$', alias: 'Email' },
        // eslint-disable-next-line
        password: { pattern: '^[A-Za-z\s]+$', alias: 'Contrasea' },
        confirmPasword: { pattern: '', alias: 'Contrasea' }
      }
    }
  },
  methods: {
    validateData (form, meta) {
      let errs = []
      for (const key in form) {
        let input = form[key];
        let expression = false
        if (meta[key]) {
          let regex = new RegExp(meta[key].pattern)
          expression = !regex.exec(input)
        }       
        if (expression) {
          errs.push(` ${meta[key].alias}`)
        }
      }

      if (errs.length > 0) {
        return `Revise los siguientes datos:${errs.toString()}`
      }
      if (this.user.password !== this.user.confirmPasword) {
        return `Tu Contrasea no pudo ser confirmada`
      }
      else {
        this.passwordVerified = true
      }
    },
    signUp (e) {      
      e.preventDefault()
      let _this = this
      let errs = _this.validateData(_this.user, _this.meta)
      if (errs) { _this.err = errs }
      else {        
        _this.loading = true
        _this.$http.post('/signup', _this.user ).then(() => {
          _this.loading = false
          this.$store.dispatch('sign_in', this.user).then(() => {
            this.inn = `Usuario registrado correctamente, ${this.user.userName}!`
            setTimeout(() => {
              this.inn = ''
              Bus.$emit('userWin')
              }, 3000);
          })
        }).catch((err) => {
          _this.loading = false
          _this.err = 'Error al crear el Usuario'
          console.error('Connection error!', err)
        })
      }
    }
  }
}
</script>

