<template>
  <div class="clasesScope">
    <Loader v-if="loading" />
    <ClaseIndex v-for="(clase, index) in clases" :key="index" 
      :info="clase" :index="index+1" 
    />
    <!-- <code><pre>{{$data}}</pre></code> -->
  </div>
</template>

<script>

import ClaseIndex from '@/components/ClaseIndex.vue'
import Loader from '@/components/Loader'

import Bus from '@/helpers/Bus'

export default {
  name: 'ClasesIndex',
  components:{
    ClaseIndex,
    Loader
  },
  data() {
    return {
      clases: [],
      loading: true
    }
  },
  methods: {
    getModules() {
      this.loading = true
      this.$http.get('modules/'+ this.$store.getters.user._id,
      { headers: {'Authorization': `Bearer ${this.$store.state.session.token}`} })
      .then(res => {
        this.loading = false
        this.clases = res.data.modules
        Bus.$emit('nextData')
      })
    }
  },
  mounted() {
    Bus.$on('loadModules', this.getModules)
  },
}
</script>

<style scoped lang="scss">

  .clasesScope{
    position: relative;
    @include edItem(100);
    @include flex;
    @include fromTo(s,l){
      padding: 0
    }
  }

</style>
